<template>
  <div>
    <h2>购物车</h2>
    <ul>
      <li v-for="(fruit, index) of fruits" :key="index">
        种类:{{ fruit.name }} 单价:{{ fruit.price }}元
        <demo-counter
          :number="fruit.num"
          :index="index"
          @add="add"
          @sub="sub"
        ></demo-counter>
        价格:{{ fruit.price * fruit.num }}元
      </li>
    </ul>
    <h1>总价为:{{ totalPrice }}元</h1>
  </div>
</template>

<script>
import DemoCounter from "./DemoCounter.vue";
export default {
  components: { DemoCounter },
  data() {
    return {
      fruits: [
        { name: "苹果", price: 3, num: 0 },
        { name: "香蕉", price: 4, num: 0 },
        { name: "鸭梨", price: 2, num: 0 },
        { name: "葡萄", price: 5, num: 0 },
      ],
    };
  },
  methods: {
    add(index) {
      this.fruits[index].num++;
    },
    sub(index) {
      if (this.fruits[index].num > 0) {
        this.fruits[index].num--;
      }
    },
  },
  computed: {
    totalPrice() {
      let total = 0;
      this.fruits.map((v) => {
        total += v.price * v.num;
      });
      return total.toFixed();
    },
  },
};
</script>