<template>
  <span>
    <button @click="sub">-</button>
    <button @click="add">+</button>
    <span>{{ "数量: " + number }} </span>
  </span>
</template>

<script>
export default {
  props: ["number", "index"],
  methods: {
    sub() {
      this.$emit("sub", this.index);
    },
    add() {
      this.$emit("add", this.index);
    },
  },
};
</script>

<style>
button {
  width: 34px;
  height: 34px;
  font-size: 26px;
}
</style>